import React,{Component} from 'react';
import axios from 'axios';
import {Redirect} from 'react-router-dom';
import PropTypes from 'prop-types';
import {reactLocalStorage} from 'reactjs-localstorage';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Input from '@material-ui/core/Input';
import FileBase64 from 'react-file-base64';
import SweetAlert from 'sweetalert2-react';
import TextField from '@material-ui/core/TextField';
import $ from 'jquery';

const styles = theme => ({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    formControl: {
      margin: theme.spacing.unit,
      minWidth: 150,
    },
    selectEmpty: {
      marginTop: theme.spacing.unit * 0,
    },
  });
class SlideTopEdit extends Component{
    constructor(props){
        super(props);
        this.state = {
            placement : "Top Slide",
            link : "",
            file : "",
            show : false,
            position : "",
            id : this.props.match.params.id
        }
        this.slideHandle = this.slideHandle.bind(this);
        this.slideHandle();
    }
    
    handleChange = event => {
        this.setState({ [event.target.name]: event.target.value });
    };

    handleEditorChange = (e) => {
        this.setState({
            descriptionweb : e.target.getContent()
        }) 
    }

    slideHandle = () => {
        let id = this.props.match.params.id;
        let token= reactLocalStorage.get('auth');
        let AuthStr = "Bearer ".concat(token);
        axios.get('http://13.229.249.101/dashboard/api/slide/view/'+id, { headers: { Authorization: AuthStr } })
        .then(response => {
            const datas = response.data;
            this.setState({
                link : datas.link,
                position : datas.position
            })
        })
        .catch((error) => {
            console.log('error ' + error);
        });
    }

    componentDidUpdate(prevProps){
        if(prevProps.match.params.id !== this.props.match.params.id){
             this.slideHandle();
             let id = this.props.match.params.id;
             this.setState({
                 id : id
             });
             
        }
     }

    submitHandler = (event) => {
        const payload = {
            "file" : this.state.file,
            "placement": this.state.placement,
            "link": this.state.link,
            "position" : this.state.position,
            "id" : this.state.id
        }
        let token= reactLocalStorage.get('auth');
        const AuthStr = "Bearer ".concat(token); 
        axios.put('http://13.229.249.101/dashboard/api/slide/edit', payload, { headers: { Authorization: AuthStr } })
        .then((response) => {
            this.slideHandle();
        })
        .catch((error) => {
            console.log(error);
        });  
        event.preventDefault();    
    }

    getFiles(files){
        if(files.type === "image/jpeg"){
            var image = files.base64;
            var img = document.createElement("img")
            img.setAttribute("src", image)
            setTimeout(
                function() {
                    if(img.height === 785 && img.width === 1600){
                        this.setState({ "file" :  files.base64 });
                    }
                    else{
                        this.setState({
                            show: true, 
                            titlealert: "Image Size Failed", 
                            textalert : "Image Size 1600px X 785px"
                        })
                        $("input[type='file']").val("");
                    }
                }
                .bind(this),
                0
            );
        }
        else{
            this.setState({
                show: true, 
                titlealert: "Image Type Failed", 
                textalert : "Image Type Must .Jpg"
            });
        }
    }

    render(){
        const { classes } = this.props;
        const { placement, id } = this.state;
        const image = placement.replace(" ","")+'-'+id+".jpg";
        let page = "slide_edit";
        let role = reactLocalStorage.get('role');
        let edit = role.includes(page);

        return(
            edit === false ?<Redirect to="/admin" /> :
            <article className="article">
            <SweetAlert
                show = {this.state.show}
                title = {this.state.titlealert}
                text = {this.state.textalert}
                onConfirm={() => 
                    this.setState({ show: false, titlealert: "", textalert : "" })
                }
            />
                <div className="box-default m-3">
                <div className="box-body py-5">
                    <div className="form-group">
                        <h6>Edit Slide Top</h6>
                    </div>
                    <form  onSubmit={this.submitHandler}>
                    
                    <div className="form-group row">
                        
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Slide Top Image</label>
                        <div className="col-md-10">
                        <img src={"http://13.229.249.101/dashboard/api/web/uploads/slide/"+image} width="150" alt=""></img><br/>
                            <FileBase64
                            multiple={ false }
                            onDone={ this.getFiles.bind(this) }
                            className="w-100"
                            />
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Link</label>
                        <div className="col-md-10">
                        <Input
                            inputProps={{name: 'link'}}
                            onChange={this.handleChange}
                            value={this.state.link}
                            placeholder="Link Slide Top"
                            multiline
                            id="inputName2"
                            fullWidth
                            className="mt-0"
                        />
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Position</label>
                        <div className="col-md-10">
                        <TextField
                            id="standard-number"
                            value={this.state.position}
                            onChange={this.handleChange}
                            type="number"
                            inputProps={{name: 'position'}}
                            margin="normal"
                            className={classes.selectEmpty}
                        />
                        </div>
                    </div>
                    

                    <div className="form-group row">
                        <div className="col-md-2"></div>
                        <div className="col-md-10">
                        <Button variant="contained" className="btn-w-md" type="submit"> Submit </Button>
                        </div>
                    </div>
                    </form>

                </div>
                </div>
            </article>
        )
    }
}
SlideTopEdit.propTypes = {
    classes: PropTypes.object.isRequired,
  };
export default  withStyles(styles)(SlideTopEdit);